import React, { useEffect } from 'react'
import ArrowRightIcon from '@mui/icons-material/ArrowRight'
import PercentIcon from '@mui/icons-material/Percent'
import RedeemIcon from '@mui/icons-material/Redeem'
import { useNavigate } from 'react-router-dom'
import { useFormData } from '../../../../contexts/FormDataContext'
import './PaymentPage.css'

const PaymentPage = ({ submitBooking, booking, setBooking }) => {
  const formData = useFormData()
  const navigate = useNavigate()
  useEffect(() => {
    if (!formData.flight) {
      navigate('/selection/select/times')
    }
  }, [])
  return (
    <>
      <div className="selection-page-promotion-container">
        <header className="selection-page-promotion-header">
          <PercentIcon className="selection-page-promotion-icon" />
          Promotion Code
        </header>
        <div className="selection-page-promotion-main">
          <label htmlFor="promotion-input" className="selection-page-promotion-label">Do you have a promotion code?</label>
          <input id="promotion-input" className="selection-page-promotion-input" value={booking.promotionCode} onChange={(event) => setBooking((prev) => ({ ...prev, promotionCode: event.target.value }))} />
          <button type="button" className="selection-page-promotion-submit">Apply</button>
        </div>
      </div>
      <div className="selection-page-voucher-container">
        <header className="selection-page-voucher-header">
          <RedeemIcon className="selection-page-voucher-icon" />
          Voucher Code
          <br />
          The system will only accept a maximum of 2 vouchers,
          or a combination of 1 voucher and 1 Credit Card for payment
        </header>
        <div className="selection-page-voucher-main">
          <div htmlFor="voucher-input" className="selection-page-voucher-label">Do you have a voucher code?</div>
          <div className="selection-page-voucher-inputs">
            <div className="selection-page-voucher-input-container">
              <label htmlFor="voucher-input">Voucher code</label>
              <input id="voucher-input" className="selection-page-voucher-input" value={booking.voucherCode} onChange={(event) => setBooking((prev) => ({ ...prev, voucherCode: event.target.value }))} />
            </div>
            <button type="button" className="selection-page-voucher-submit">Apply Voucher</button>
          </div>
        </div>
      </div>
      <div>Payment</div>
      <div>
        pay
      </div>
      <div className="selection-page-continue-container">
        <button type="button" className="selection-page-continue-btn" onClick={submitBooking}>
          CONTINUE
          <ArrowRightIcon />
        </button>
      </div>
    </>
  )
}
export default PaymentPage
